import Bmob from "hydrogen-js-sdk";

let that;
let phone_number = "";
export default {
	data() {
		return {
			code_text: "验证码",
			code_button_state: false
		}
	},
	onLoad() {
		that = this;
	},
	methods: {
		//手机输入触发
		get_InputPhone(e) {
			phone_number = e.detail.value;
		},

		//获取验证码点击
		get_smscode: function() {
			if (phone_number.length < 11) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: "none"
				})
			} else {
				let params = {
					mobilePhoneNumber: phone_number, //string
					template: "积木舟"
				}
				Bmob.requestSmsCode(params).then(function(response) {
						uni.showToast({
							title: '发送成功'
						});
						that.code_button_state = true;
						that.code_text = 60;

						let code_down = setInterval(function() {
							that.code_text = that.code_text - 1
							if (that.code_text == 0) {
								clearInterval(code_down);
								that.code_button_state = false;
								that.code_text = "验证码";
							}
						}, 1000);

					})
					.catch(function(error) {
						wx.showToast({
							title: '发送失败',
							icon: "none"
						})
					});
			}
		},

		//登陆提交
		formSubmit(e) {
			console.log(e)
			let phone =e.detail.value.phone;
			let sms_code = e.detail.value.sms_code;

			if (e.detail.value.phone.length < 11) {
				uni.showToast({
					title: '手机格式错误',
					icon: "none"
				})
			} else if (e.detail.value.sms_code < 6) {
				uni.showToast({
					title: '验证码格式错误',
					icon: "none"
				})
			} else {
				let smsCode = sms_code
				let data = {
				  mobilePhoneNumber: phone
				}
				Bmob.verifySmsCode(smsCode ,data).then(function(response) {
					console.log(response);
					const query = Bmob.Query("_User");
					query.equalTo("mobilePhoneNumber", "==", phone);
					query.find().then(res => {
						console.log(res)
						uni.setStorageSync("user", res[0])
						uni.setStorageSync("masterId", res[0].objectId)
						uni.setStorageSync("identity", 1); //1是老板，2是员工
						uni.setStorageSync("uid", res[0].objectId)
						uni.switchTab({
							url: "/pages/tarBar/index"
						});

						Bmob.User.login(res[0].username, res[0].username).then(res => {
							console.log(res)
						}).catch(err => {
							console.log(err)
						});

					});

				}).catch(function(error) {
					uni.showToast({
						title: '验证码或手机号错误',
						icon: "none"
					})
				});
			}

		},

	}
}
